/*#region ************** Global **************/
body {
  line-height: 1.25;
  font-size: 87.5%;
  font-family: "Open Sans", Verdana, Calibri, Helvetica, Arial, sans-serif;
  color: #eaeaea; }

.page {
  max-width: 1520px;
  margin: 0 auto; }

.inner {
  margin: 0 auto;
  width: 88%; }

/*#endregion*/
/*#region ************** Header **************/
header {
  position: fixed; }

header nav {
  font-family: "Open Sans Condensed", "Verdana", sans-serif;
  font-size: 2.1em;
  text-align: center;
  background: rgba(51, 51, 51, 0.85);
  margin-top: 35px;
  padding: 10px;
  min-width: 165px;
  border-radius: 0 36px;
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.06), 0 1px 3px rgba(0, 0, 0, 0.15); }

header nav a,
header nav a:visited {
  text-align: right;
  color: rgba(140, 177, 24, 0.84) !important;
  -webkit-text-stroke: 1px rgba(140, 177, 24, 0.84);
  text-stroke: 1px rgba(140, 177, 24, 0.93);
  text-shadow: 0 3px 3px #000;
  display: block;
  margin-right: 18px;
  line-height: 1.2;
  transition: 0; }

header nav a:hover,
.backNav a:hover i {
  text-shadow: 0 0 6px #8cb118; }

header nav a:active {
  background: transparent; }

header nav a i {
  font-size: 0.6em;
  -webkit-text-stroke: 0;
  text-stroke: 0;
  display: block;
  float: right;
  margin-left: 15px;
  padding-top: 10px; }

header nav a:hover i,
.home nav .icon-home,
.games nav .icon-controllernes,
.music nav .icon-music,
.playground nav .icon-beaker,
.portfolio nav .icon-briefcase,
.about nav .icon-user,
.contact nav .icon-phone {
  color: #dd4040 !important;
  text-shadow: 0 3px 3px #000;
  -webkit-text-stroke: 0;
  text-stroke: 0; }

/* First Nav */
header nav:first-child {
  border-radius: 43px 0;
  text-indent: 9px;
  padding-bottom: 20px; }

header nav:first-child i {
  float: none;
  margin-left: 0;
  padding-top: 0; }

header nav:first-child a {
  text-align: center;
  line-height: 1.1 !important;
  margin-bottom: 0;
  margin-right: 0; }

/* Second Nav */
.hdrNav2 {
  margin-top: 30px;
  padding-top: 5px; }

.hdrNav2 ul a {
  margin-bottom: 4px;
  text-align: center; }

.hdrNav2 .icon-controllernes {
  font-size: 0.72em; }

/* Back Nav */
.backNav span {
  display: inline; }

.backNav a:hover span {
  color: #8cb118 !important; }

/* Playground Nav */
header nav ul {
  list-style-type: none;
  font-size: 58%;
  width: 90%;
  margin: 10px auto 4px auto;
  background: rgba(31, 31, 27, 0.6);
  padding: 10px 0;
  border-radius: 1px; }

header nav ul a {
  margin-right: 0 !important; }

header nav ul a:active {
  margin-bottom: 4px; }

header nav ul #cube {
  margin: inherit;
  width: inherit;
  height: inherit; }

.nav1 .playgroundNav li:nth-child(1) a,
.nav2 .playgroundNav li:nth-child(2) a,
.nav3 .playgroundNav li:nth-child(3) a,
.nav4 .playgroundNav li:nth-child(4) a,
.nav5 .playgroundNav li:nth-child(5) a,
.nav6 .playgroundNav li:nth-child(6) a {
  color: #ff6a00 !important;
  -webkit-text-stroke: 1px #ff6a00;
  text-stroke: 1px #ff6a00; }

.nav1 .playgroundNav li:nth-child(1) a:hover,
.nav2 .playgroundNav li:nth-child(2) a:hover,
.nav3 .playgroundNav li:nth-child(3) a:hover,
.nav4 .playgroundNav li:nth-child(4) a:hover,
.nav5 .playgroundNav li:nth-child(5) a:hover,
.nav6 .playgroundNav li:nth-child(6) a:hover {
  text-shadow: #ff6a00 0 0 18px; }

.dPlaygroundNav {
  display: none; }

.hdrNav2 a i {
  display: inline-block;
  -webkit-animation: vWave 0.3s linear;
  animation: vWave 0.3s linear; }

.hdrNav2 a:nth-of-type(1) i {
  -webkit-animation-delay: 0.15s;
  animation-delay: 0.15s; }

.hdrNav2 a:nth-of-type(2) i {
  -webkit-animation-delay: 0.19s;
  animation-delay: 0.19s; }

.hdrNav2 a:nth-of-type(3) i {
  -webkit-animation-delay: 0.23s;
  animation-delay: 0.23s; }

.hdrNav2 a:nth-of-type(4) i {
  -webkit-animation-delay: 0.27s;
  animation-delay: 0.27s; }

.hdrNav2 a:nth-of-type(5) i {
  -webkit-animation-delay: 0.31s;
  animation-delay: 0.31s; }

.hdrNav2 a:nth-of-type(6) i {
  -webkit-animation-delay: 0.35s;
  animation-delay: 0.35s; }

.hdrNav2 a:nth-of-type(7) i {
  -webkit-animation-delay: 0.39s;
  animation-delay: 0.39s; }

.hdrNav2 a:nth-of-type(8) i {
  -webkit-animation-delay: 0.43s;
  animation-delay: 0.43s; }

/*#endregion*/
/*#region *************** Main ***************/
.main {
  margin-top: 40px;
  padding-left: 215px; }

.colL {
  float: left;
  width: 37%;
  min-width: 121px;
  margin-bottom: 120px; }

.colL ul {
  font-size: 1.15em; }

.colR {
  float: right;
  width: 58%;
  padding-bottom: 13px; }

.colR > div {
  padding-bottom: 13px !important; }

.colR.noHdr {
  margin-top: 51px; }

/*#endregion*/
/*#region ************** Footer **************/
footer {
  position: fixed;
  bottom: 0;
  width: 100%; }

footer::before {
  display: block;
  height: 120px; }

.outFooter {
  /* height: 68px; */
  position: relative;
  bottom: -26px;
  margin-top: -94px;
  width: 100%;
  height: 28px;
  padding: 20px 0 19px;
  font-size: 127%;
  background: rgba(58, 57, 58, 0.6);
  border-top: 1px solid #464546; }

.outFooter:before,
.outFooter:after {
  content: "";
  position: absolute;
  left: 0;
  background: rgba(50, 49, 50, 0.78);
  width: 100%;
  height: 100%;
  z-index: -1; }

.outFooter:before {
  /* bottom of stack */
  background: rgba(50, 49, 50, 0.45);
  top: -20px;
  height: 88px; }

.outFooter:after {
  top: -10px;
  height: 78px; }

.inFooter {
  width: 70%;
  margin: 0 auto; }

footer a {
  font-size: 1.25em;
  vertical-align: -2px;
  display: inline-block;
  -webkit-animation: wave 0.3s linear;
  animation: wave 0.3s linear; }

footer a:nth-of-type(1) {
  -webkit-animation-delay: 0.15s;
  animation-delay: 0.15s; }

footer a:nth-of-type(2) {
  -webkit-animation-delay: 0.19s;
  animation-delay: 0.19s; }

footer a:nth-of-type(3) {
  -webkit-animation-delay: 0.23s;
  animation-delay: 0.23s; }

footer a:nth-of-type(4) {
  -webkit-animation-delay: 0.27s;
  animation-delay: 0.27s; }

footer a:nth-of-type(5) {
  -webkit-animation-delay: 0.31s;
  animation-delay: 0.31s; }

footer a:nth-of-type(6) {
  -webkit-animation-delay: 0.35s;
  animation-delay: 0.35s; }

footer a:nth-of-type(7) {
  -webkit-animation-delay: 0.39s;
  animation-delay: 0.39s; }

footer a:nth-of-type(8) {
  -webkit-animation-delay: 0.43s;
  animation-delay: 0.43s; }

footer .footL {
  float: left; }

footer .footL a {
  margin: 0 0.18em; }

footer .name {
  display: inline-block;
  text-indent: -999em; }

footer .icon-youtube-sign {
  font-size: 1.254em;
  vertical-align: -2px; }

footer .footAbout .name {
  text-indent: 0; }

footer .footContact {
  /*margin-right: 1.3em !important;*/ }

footer .footR {
  float: right;
  color: #aaa; }

@-webkit-keyframes wave {
  0% {
    transform: translateY(0); }

  50% {
    transform: translateY(-10px); }

  100% {
    transform: translateY(0); } }

@keyframes wave {
  0% {
    transform: translateY(0); }

  50% {
    transform: translateY(-10px); }

  100% {
    transform: translateY(0); } }

@-webkit-keyframes vWave {
  0% {
    transform: translateX(0); }

  50% {
    transform: translateX(10px); }

  100% {
    transform: translateX(0); } }

@keyframes vWave {
  0% {
    transform: translateX(0); }

  50% {
    transform: translateX(10px); }

  100% {
    transform: translateX(0); } }

/*#endregion*/
/*#region ********** Page Templates **********/
/*#region Full Width */
.pageFullW .page {
  max-width: 98%; }

.pageFullW .inner {
  width: 99.1%; }

.pageFullW header {
  margin-right: 0; }

.pageFullW header nav:first-child {
  text-indent: -8px;
  margin: 18px 0 20px 0;
  padding: 4px 0px 6px 0;
  border-radius: 25px 0;
  width: 105px; }

.pageFullW .main {
  margin: 0 auto inherit auto;
  padding: 3px 26px 26px 28px; }

.pageFullW .main .titleHeader {
  margin-top: 8px; }

.pageFullW canvas {
  box-shadow: 0 0 26px -2px #000; }

/*#endregion*/
/*#region Absolute Hover */
.absHover .colR div > p {
  margin-bottom: 12px;
  width: 80%; }

.absHover .colR > div {
  position: absolute;
  display: none;
  width: 44%;
  max-width: 838px; }

.absHover .colR #divDefault {
  display: block; }

/*#endregion*/
/*#endregion*/
/*#region ************ Responsive ************/
@media screen and (max-width: 1350px) {
  .inner {
    width: 97%; }

  .main {
    padding-left: 207px; } }
@media screen and (max-width: 1265px) {
  .inner {
    width: 65%; }

  .main {
    padding-left: 215px; }

  .colL {
    width: 100% !important;
    margin-bottom: 0;
    max-width: 528px; }

  .colR {
    float: left;
    width: 100% !important;
    margin-top: 25px; }

  .home .colR {
    float: left;
    width: 75%; }

  .absHover .colR > div {
    width: 100%;
    max-width: 528px; }

  .absHover .colR div p {
    width: 100%;
    margin-bottom: 15px; }

  footer a {
    margin: 0 !important; }

  .inFooter {
    width: 96%; } }
@media screen and (max-width: 1145px) {
  .inner {
    width: 75%; } }
@media screen and (max-width: 1065px) {
  .inner {
    width: 85%; } }
@media screen and (max-width: 920px) {
  .inner {
    width: 95%; }

  .main {
    padding-left: 207px; } }
@media screen and (max-width: 800px) {
  /* vertical footer */
  .inner {
    width: 98%; }

  header {
    position: static;
    width: 100%;
    float: none;
    margin-top: 15px; }

  header nav {
    width: 80%;
    margin: 0 auto; }

  header nav a {
    text-align: center; }

  header nav a span {
    display: none; }

  .hdrNav2 {
    margin-top: 22px; }

  footer {
    position: static;
    margin-top: 0; }

  .outFooter {
    height: 372px; }

  .inFooter {
    margin-top: -4px; }

  footer a {
    display: block;
    margin-bottom: 8px !important;
    font-size: 24px;
    text-align: right; }

  footer .footL {
    float: none;
    width: 192px;
    margin: 0 auto;
    display: block;
    padding-bottom: 30px; }

  footer .footR {
    display: none; }

  footer .name {
    text-indent: 0;
    width: 139px;
    padding-right: 6px; }

  footer .footContact {
    margin-right: 0 !important;
    margin-bottom: 25px !important; }

  .main {
    text-align: center;
    overflow: visible;
    padding-left: 0;
    margin: 25px auto 0;
    width: 77%;
    padding-right: 28px; }

  .colL {
    display: none;
    max-width: 100%; }

  .home .colL, .music .colL {
    display: block; }

  .home .colR {
    padding-left: 2.5%; }

  .colL, .colR {
    float: none !important; }

  .music .colR {
    border-top: 0; }

  .absHover .colR > div {
    display: block;
    width: 100%;
    position: static;
    margin-bottom: 20px;
    max-width: 100%; }

  .absHover .colR > div:last-child {
    margin-bottom: 100px; }

  #divDefault {
    display: none !important; }

  .absHover .colR div > p {
    width: 100%; }

  ul {
    list-style-type: none;
    padding: 0; }

  .bigBtn {
    margin: 30px auto 0; }

  .enlSite {
    float: none !important; } }
/*#endregion*/
/*#region ************** Home ****************/
.home .colR {
  margin-bottom: 110px;
  width: 56%; }

/*#endregion*/
/*#region ************** Home ****************/
.games .techUsed {
  margin-top: 26px;
  margin-bottom: 22px; }

.games .techUsed .lh {
