.uWrap .list ul {
  width: 203px;
  list-style-type: none;
  padding: 0 0 0 2px;
  margin-left: 0;
  box-shadow: 4px 3px 18px rgba(0, 0, 0, 0.8);
  background-color: rgba(19, 19, 19, 0.7); }

.uWrap .list li {
  cursor: move;
  margin: 2px 0 10px;
  padding: 2px;
  font-size: 119%;
  text-shadow: 1px 1px 11px #000, 1px 1px 12px #000; }

.uWrap .list li:first-child {
  margin-top: 0; }

.uWrap .list li:hover {
  color: #61b0d7;
  text-shadow: 1px 1px 11px #001eff, 1px 1px 12px #001eff; }

.uWrap .list li .vid {
