.iframeWrap {
  margin-top: -480px; }

.floatingSun canvas:hover {
  cursor: -webkit-grab;
  cursor: -moz-grab;
  cursor: grab; }

.floatingSun canvas:active {
  cursor: -webkit-grabbing;
  cursor: -moz-grabbing;
  cursor: grabbing; }

