/*#region **** override browser elts ****/
body {
  background-color: #bbb;
  background-image: url(/img/jonBig.jpg);
  background-position-x: 51% !important;
  /* doesn't work in firfox */
  background-repeat: repeat-y;
  background-attachment: fixed;
  color: #d2d2d2;
  margin: 0; }

input,
textarea {
  outline-color: #888; }

input:focus,
textarea:focus {
  box-shadow: 2px 2px 16px 2px rgba(0, 0, 0, 0.45); }

input[type=submit],
button {
  transition: all .35s ease-out; }

input[type=submit]:hover,
input[type=color]:hover,
button:hover {
  cursor: pointer; }

h1, h2, h3, h4, h5, h6 {
  margin: .4em 0 .6em 0;
  font-size: 1.62em;
  letter-spacing: -0.03em;
  color: #ff6a00;
  text-shadow: 0 2px 3px #111; }

a {
  color: rgba(92, 173, 214, 0.8);
  text-decoration: none;
  outline: none; }

a:hover {
  color: #5cadd6;
  text-shadow: #61b0d7 0 0 6px; }

a:visited,
a:active {
  color: #5dafd7; }

a:focus {
  outline: 0; }

/*#endregion*/
/*#region Fonts */
@font-face {
  font-family: "icomoon";
  src: url("/font/icomoon.eot");
  src: url("/font/icomoon.svg#icomoon") format("svg"), url("/font/icomoon.eot?#iefix") format("embedded-opentype"), url("/font/icomoon.ttf") format("truetype"), url("/font/icomoon.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

[class*="icon-"] {
  font-family: "icomoon";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

