/*#region ************ Utilities *************/
.clr:before,
.clr:after {
  content: " ";
  display: table; }

.clr:after,
.clrB {
  clear: both; }

br.clr {
  display: none; }

.lh {
  list-style-type: none;
  font-weight: 700;
  margin: 12px 0 8px 0;
  padding-left: 0; }

.noHeaderR {
  margin-top: .55em; }

.subHead {
  font-size: 1.15em;
  margin: .5em 0 .3em 0; }

.opaque {
  opacity: 1; }

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.noBullet {
  list-style-type: none; }

.txtBg, .colL,
.colR > div,
.twitter-timeline-rendered,
.music .colR iframe {
  background: rgba(51, 51, 51, 0.85);
  padding: 1px 15px 5px 15px;
  border-radius: 2px;
  box-shadow: 0 1px 7px rgba(0, 0, 0, 0.06), 0 1px 3px rgba(0, 0, 0, 0.15); }

.twitter-timeline, .music .colR iframe {
  padding: 0; }

.bigBtn {
  display: block;
  margin-top: 10px;
  width: 150px;
  padding: 9px 14px;
  border-radius: 10px;
  font-weight: bold;
  font-size: 1.2em;
  color: #eaeaea !important;
  background: rgba(107, 107, 107, 0.64);
  box-shadow: 0 3px 0 rgba(54, 54, 54, 0.94);
  opacity: 1; }

.bigBtn span {
  padding-right: 10px;
  vertical-align: -1px; }

.bigBtn:hover,
.bigBtn:focus {
  background: #cc5500;
  box-shadow: 0 3px 0 #662b00; }

.bigBtn:active {
  box-shadow: none;
  margin-top: 11px;
  transition: all 0.05s; }

.colL .bigBtn {
  margin-left: -20px;
  text-align: center; }

/*#endregion*/
